export const SKILL_DICTIONARY = {
    acr: 'acrobatics',
    arc: 'arcana',
    ath: 'athletics',
    cra: 'crafting',
    dec: 'deception',
    dip: 'diplomacy',
    itm: 'intimidation',
    med: 'medicine',
    nat: 'nature',
    occ: 'occultism',
    prf: 'performance',
    rel: 'religion',
    soc: 'society',
    ste: 'stealth',
    sur: 'survival',
    thi: 'thievery',
} as const

export const SKILL_ABBREVIATIONS = Object.keys(SKILL_DICTIONARY) as (keyof typeof SKILL_DICTIONARY)[]

export const SKILL_LONG_FORMS = Object.values(SKILL_DICTIONARY)

export function getTranslatedSkills() {
    const skillList = (CONFIG as unknown as ConfigPF2e).PF2E.skillList
    return Object.entries(skillList).reduce((result: Record<string, string>, [key, value]) => {
        return {
            ...result,
            [key]: game.i18n.localize(value).toLocaleLowerCase(game.i18n.lang),
        }
    }, {})
}
