import { getSourceId, includesSourceId } from '@utils/foundry/flags'

type ActorItemTypes<A extends Actor> = (keyof A['itemTypes'])[]

function getItemSourceIdCondition(sourceId: ItemUUID | ItemUUID[]) {
    return Array.isArray(sourceId)
        ? (item: Item) => includesSourceId(item, sourceId)
        : (item: Item) => getSourceId(item) === sourceId
}

export function getItems(actor: Actor, itemTypes?: ActorItemTypes<Actor>) {
    return (itemTypes ? itemTypes.flatMap(type => actor.itemTypes[type as string]) : actor.items) as Embedded<Item>[]
}

export function findItemWithSourceId<I extends Item>(actor: Actor, sourceId: ItemUUID, itemTypes?: ActorItemTypes<Actor>) {
    return getItems(actor, itemTypes).find(getItemSourceIdCondition(sourceId)) as unknown as Embedded<I>
}

export function hasItemWithSourceId<A extends Actor, I extends Item = CollectionValue<A['items']>>(
    actor: A,
    sourceId: ItemUUID | ItemUUID[],
    itemTypes?: ActorItemTypes<A>
) {
    return getItems<A, I>(actor, itemTypes).some(getItemSourceIdCondition(sourceId))
}

export function filterItemsWithSourceId<I extends Item>(
    actor: Actor,
    sourceId: ItemUUID | ItemUUID[],
    itemTypes?: ActorItemTypes<Actor>
) {
    return getItems(actor, itemTypes).filter(getItemSourceIdCondition(sourceId)) as unknown as Embedded<I>[]
}
