import { MODULE_ID } from '@utils/module'

type OwnModule = 'anonymous' | 'idleuh' | 'pf2e-hero-actions' | 'pf2e-dailies'

type ReturnedModule<TApi extends ModuleApi = ModuleApi> = Module<TApi> | undefined
type ReturnedModuleApi<TApi extends Record<string, any>> = Module<TApi>['api'] | undefined

export function getModule<T extends ModuleApi = ModuleApi>(id: string | OwnModule) {
    return game.modules.get(id) as ReturnedModule<T>
}

export function getCurrentModule<TApi extends ModuleApi = ModuleApi>() {
    return getModule(MODULE_ID) as Module<TApi>
}

export function getModuleApi<TApi extends Record<string, any>>(id: string | OwnModule) {
    return getModule<TApi>(id)?.api as ReturnedModuleApi<TApi>
}
